<template>
    <div>
    <form @submit.prevent="assignNegocio(negocio)" >
        <v-card class="mx-auto" 
       
        :loading="loading"
        :disabled="loading"
        max-width="500">
    <v-img
      src="@/assets/img/sunshine.jpg"
      class="white--text align-end"
      gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)"
      height="250px" 
      dark
    >
    
    <v-card-title class="display-1">
      Criar Novo Neg贸cio
    </v-card-title>
    
    </v-img>


<!--     <v-card-subtitle>
      1,000 miles of wonder
    </v-card-subtitle> -->

    <v-card-text>

      <v-text-field
      label="Neg贸cio"
      prepend-icon="mdi-store"  
      v-model="negocio.nome"
      :loading="loading"
      :disabled="loading"
      required
    ></v-text-field>

    <v-text-field
    label="Palavra-passe"
    v-model="negocio.senha"
    :loading="loading"
    :disabled="loading"
    required
    :type="see ? 'text' : 'password'"
    @click:append="see = !see"
    :append-icon="see ? 'mdi-eye-off' : 'mdi-eye'"
    prepend-icon="mdi-lock"
    ></v-text-field>

    </v-card-text>
    
    <v-card-actions>
        <v-btn
        type="submit"
        color="success"
        :loading="loading"
        :disabled="loading"
        class="">
        Criar
        <v-icon right> mdi-check </v-icon>
        </v-btn>
  
      <v-btn 
      :loading="loading"
      :disabled="loading"
      color="secondary"
      to="/entradaNegocio"
        >
       Entrar
        <v-icon right> mdi-login </v-icon>
      </v-btn>
    </v-card-actions>
  </v-card>
    </form>
    </div>
</template>

<script>
import service from '@/services/negocio'

export default {
    data() {
        return {
            loading:true,
            see:false,
            negocio:{
                id_negocio:"null",
                nome:"",
                senha:"",
                criado:new Date(),
            },
        }
    },
    created() {

        setTimeout(() => {


            this.loading = false
            
        }, 1500)
        
    },

    methods: {
        async assignNegocio(negocio){
            try {
                
               this.loading = true;

 
              (await service.novoNegocio(negocio));

              this.$root.toast.primary(`${negocio.nome} foi registrado com sucesso!`, 3000);

               this.loading = false;

            } catch(error) {

                this.$root.toast.err(`${error}`, 3000);
                this.loading = false;
                // this.$root.toast.err(`${error}`, 3000);


            }



        }
    },

}

</script>